r(--txt-third);\r\n  border: 3px solid var(--txt-third) !important;\r\n  margin: 20px auto;\r\n  opacity: 100%;\r\n}\r\n\r\n/*  */\r\n\r\n/* End Navbar */\r\n\r\n/* Jumbotron (Hero) */\r\n\r\n.hero {\r\n  display: flex;\r\n  align-items: center;\r\n  min-height: 100vh;\r\n  width: 100%;\r\n  margin-top: 90px;\r\n  text-align: center;\r\n  background-position: center;\r\n  background-color: black;\r\n  background-size: cover;\r\n  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),\r\n    url(\"../public/images/Beach.png\");\r\n}\r\n\r\n.hero_inner {\r\n  margin: 0 auto;\r\n  max-width: 800px;\r\n}\r\n\r\n.hero_title {\r\n  color: #dcd7c9;\r\n  font-weight: 800;\r\n  font-size: 40px;\r\n}\r\n\r\n.hero_tagline {\r\n  color: #dcd7c9;\r\n  margin-top: 16px;\r\n  font-size: 15px;\r\n  font-weight: 600;\r\n}\r\n\r\n.span_title,\r\n.span_tagline {\r\n  color: #a27b5c;\r\n}\r\n\r\n/* End Jumbotron */\r\n\r\n/* Destination */\r\n.card-destination-left .img-left,\r\n.card-destination-right .img-right {\r\n  object-fit: cover;\r\n}\r\n\r\n.card-destination-left .img-left {\r\n  max-height: 100%;\r\n  height: 300px;\r\n}\r\n\r\n.card-destination-right .img-right {\r\n  max-height: 100%;\r\n  height: 300px;\r\n}\r\n\r\n.content-card {\r\n  cursor: pointer;\r\n  position: absolute;\r\n  background-color: rgba(0, 0, 0, 0.8);\r\n  top: 0;\r\n  width: 100%;\r\n  height: 100%;\r\n  opacity: 0;\r\n}\r\n\r\n.item-card {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  justify-content: center;\r\n  width: 100%;\r\n  height: 100%;\r\n}\r\n\r\n.card_items:hover {\r\n  transition: transform 0.5s;\r\n  cursor: pointer;\r\n}\r\n\r\n.content-card:hover {\r\n  opacity: 100%;\r\n  display: block;\r\n  padding: 30px;\r\n  text-wrap: wrap;\r\n  text-align: center;\r\n}\r\n\r\n.destination-container {\r\n  padding: 0 20px;\r\n}\r\n\r\n.content-card p a {\r\n  color: #dcd7c9;\r\n}\r\n\r\n.content-card p a:hover {\r\n  color: var(--txt-third);\r\n}\r\n\r\n/* End Destination */\r\n\r\n/* Category */\r\n\r\n.category {\r\n  background-color: var(--bg-third);\r\n  padding: 80px 0;\r\n}\r\n\r\n.kategori_card {\r\n  min-height: 150px;\r\n  min-width: 150px;\r\n  background-color: var(--bg-secondary);\r\n  border-radius: 30px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  transition: transform 0.2s;\r\n}\r\n\r\n.kategori_items :hover {\r\n  transform: scale(1.2);\r\n  cursor: pointer;\r\n}\r\n\r\n.kategori_card a:hover,\r\n.kategori_card h5:hover,\r\n.kategori_card img:hover {\r\n  transform: scale(1);\r\n}\r\n\r\n.kategori_card img {\r\n  max-width: 50px;\r\n  max-height: 50px;\r\n  object-fit: contain;\r\n}\r\n\r\n.kategori_card h5 {\r\n  color: var(--txt-third);\r\n  margin-top: 15px;\r\n  margin-bottom: 0;\r\n  font-weight: 600;\r\n}\r\n\r\n/* Service */\r\n\r\n.service {\r\n  background-color: var(--bg-third);\r\n  overflow-x: hidden !important;\r\n}\r\n\r\n.img-cover {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  width: 100%;\r\n}\r\n\r\n.icon-service {\r\n  width: 100px;\r\n  height: 100px;\r\n  background-color: var(--bg-secondary);\r\n  padding: 20px;\r\n  object-fit: contain;\r\n  border-radius: 20%;\r\n}\r\n\r\n.service-title {\r\n  font-size: 22px;\r\n  font-weight: 600;\r\n  margin: 20px 0;\r\n}\r\n\r\n.service-subtitle {\r\n  line-height: 30px;\r\n  font-weight: 400;\r\n}\r\n\r\n/* Swiper */\r\n\r\n.swiper {\r\n  width: 100%;\r\n  height: 100%;\r\n}\r\n\r\n.swiper-slide {\r\n  text-align: center;\r\n  font-size: 18px;\r\n  background: #fff;\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  width: 100%;\r\n}\r\n\r\n.swiper-button-next,\r\n.swiper-button-prev {\r\n  background-color: rgba(0, 0, 0, 0.5);\r\n  padding: 30px !important;\r\n  color: var(--bg-third);\r\n  font-weight: 900;\r\n}\r\n\r\n.item-top-penginapan img {\r\n  height: 75vh;\r\n  width: 100%;\r\n}\r\n\r\n.item-wisata-lainnya .card-wis